import React, { Component } from 'react'
import { Tabs, Panel, TabsItem, PanelHeader, Div, Button, FixedLayout, ActionSheetItem, ActionSheet} from '@vkontakte/vkui';

import Icon28AddOutline from '@vkontakte/icons/dist/28/add_outline';
import Icon28ViewOutline from '@vkontakte/icons/dist/28/view_outline';
import Icon28ChecksOutline from '@vkontakte/icons/dist/28/checks_outline';
import Icon28DoneOutline from '@vkontakte/icons/dist/28/done_outline';

export default class Aviasales extends Component {
    state = {
        activeTab:'aviasales',
        activeViget:'aviasales'
    }

    openMenu = () => {
        this.props.setPopout(
        <ActionSheet onClose={() => this.props.setPopout(null)}>
            <ActionSheetItem autoclose before={this.state.activeViget == 'aviasales' ? <Icon28ChecksOutline />: <Icon28DoneOutline />} onClick={() => this.setState({ activeViget: 'aviasales' })}>Aviaseles</ActionSheetItem>
            <ActionSheetItem autoclose before={this.state.activeViget == 'trip' ? <Icon28ChecksOutline />: <Icon28DoneOutline />} onClick={() => this.setState({ activeViget:'trip'})}>trip</ActionSheetItem>
        </ActionSheet>
        )
    }

    render() {
        const { id } = this.props;
        const { activeTab, activeViget } = this.state;
        return (
            <Panel id={id}>
                <PanelHeader separator={false}>Авиабилеты</PanelHeader>
                <Tabs>
                    <TabsItem selected={activeTab === 'aviasales'} onClick={() => this.setState({ activeTab: 'aviasales'})}>Виджеты</TabsItem>
                    <TabsItem selected={activeTab === 'skyscanner'} onClick={() => this.setState({ activeTab: 'skyscanner'})}>Метапоисковики</TabsItem>
                </Tabs>

                {activeTab == 'aviasales' && activeViget == 'aviasales' &&
                    <iframe src="https://www.aviasales.ru/" height={window.screen.height} width="100%" align="center"></iframe>    
                }
                {activeTab == 'aviasales' && activeViget == 'trip' && 
                <iframe src='https://ru.trip.com/?locale=ru_ru' height={500} width="100%" align="center"></iframe>
                }

                <FixedLayout vertical='bottom'>
                    <Div align="center" style={{ paddingBottom:6 }}>
                        <Button className='circle_button' onClick={() => {this.openMenu()
                        }}><Icon28ViewOutline />
                        </Button>
                    </Div>
                </FixedLayout>
            </Panel>
        )
    }
}